<template>
  <div class="login">
    <div class="img-container">
      <Logo/>
      <div class="img">
        <img src="@/assets/register-IMG.png" alt="">
      </div>
    </div>
    <div class="login-form">
      <h2>¡Bienvenid@ de vuelta!</h2>
      <form action="#" @submit.prevent="handleSubmit">
        <Input
          class="Input"
          label="Email"
          :value="email"
          input-placeholder="Email"
          @inputChange="(value) => email = value"
        />
        <Input
          class="Input"
          label="Contraseña"
          :value="password"
          input-type="password"
          input-placeholder="Contraseña"
          @inputChange="(value) => password = value"
        />
        <Button btnType="cta" class="btn--primary">Ingresar</Button>
      </form>

    </div>
  </div>
</template>

<script>
import Button from '@/components/Button.vue';
import Input from '@/components/Input.vue';
import Logo from '@/components/Logo.vue';
import { mapActions, mapState } from 'vuex';

export default {
  name: 'Registro',
  components: {
    Input,
    Button,
    Logo,
  },
  data() {
    return {
      email: '',
      password: '',
    };
  },
  watch: {
    isLogged(value) {
      if (value) this.$router.replace('/');
      else alert('Your email or password is incorrect.');
    },
  },
  computed: {
    ...mapState({
      isLogged: (state) => state.authStore.isLogged,
    }),
  },
  methods: {
    ...mapActions({
      login: 'authStore/login',
    }),
    handleSubmit() {
      this.login({
        email: this.email,
        password: this.password,
      });
    },
  },
};
</script>

<style lang="scss" scoped>
@import '@/assets/styles/main';

.login {
  min-height: 100vh;
  display: grid;
  grid-template-columns: 60% 40%;
  align-items: center;
}

.img-container {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-items: center;
  padding: 2em;

  background-color: #F8F8FA;

  .img { margin: auto; }
}

.login-form {
  padding: 2em;

  h2 {
    margin-bottom: 2em;

    font: $text-mdlg $font-primary;
    font-weight: $bold;
    text-align: center;
  }

  form {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .Input + .Input {
    margin-top: 1.25em;
  }

  .Input, Button { width: 80%; }

  Button {
    margin-top: 2.5em;

    text-align: center;
  }
}

@media (max-width: 900px) {

  .login {
    display: flex;
    flex-flow: column;
  }

  .img-container {
    width: 100%;
    height: auto;
    padding: 1em;

    background-color: white;

    .img { display: none; }
  }

  .login-form {
    width: 100%;
    margin: auto;

    Button { width: unset; }
  }

}

@media (max-width: 450px) {

  .login-form {
    .Input, Button { width: 90%; }
  }
}
</style>
